@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base{
   li{
    @apply cursor-pointer flex flex-col items-center font-medium
   }

   li:hover .line{
    @apply opacity-100 transition duration-300 ease-in-out
   }

   li .line{
    @apply w-4 h-0.5 sm:bg-white bg-black opacity-0
   }
}
